import React, {useState} from 'react';
import { extractDCTWatermark } from '../lib/dct.js';

function VerifyTab() {
  const [result, setResult] = useState(null);
  function onDropFile(e) {
    e.preventDefault();
    const f = e.dataTransfer.files[0];
    const reader = new FileReader();
    reader.onload = async (ev) => {
      const blob = new Blob([ev.target.result]);
      const img = await createImageBitmap(blob);
      const c = document.createElement('canvas');
      c.width = img.width; c.height = img.height;
      const ctx = c.getContext('2d');
      ctx.drawImage(img,0,0);
      const imgData = ctx.getImageData(0,0,c.width,c.height);
      const bits = extractDCTWatermark(imgData, 8);
      // compute confidence as percentage of bits matching expected pattern logic
      const confidence = (bits.filter(b=>b===1).length / bits.length) * 100; // demo heuristic
      setResult({ bits, confidence });
    };
    reader.readAsArrayBuffer(f);
  }

  return (
    <div>
      <div onDrop={onDropFile} onDragOver={(e)=>e.preventDefault()} style={{border:'2px dashed #ccc',padding:'20px'}}>
        Drop image here to verify
      </div>
      {result && <div>Verified PixGuard<br/>Confidence: {Math.round(result.confidence)}%<br/>Bits: {result.bits.join('')}</div>}
    </div>
  );
}

export default VerifyTab;
