// small DCT helpers and embed/extract functions
// Use 8x8 block DCT on luminance (Y) channel
export function embedDCTWatermark(imgData, bitsString) {
  // convert to YCbCr, process Y channel per 8x8, modify mid-freq coefficients by tiny delta
  const w = imgData.width, h = imgData.height;
  const data = imgData.data;
  // very small implementation: for first N blocks, slightly increment coefficient at (u=3,v=2) for bit==1 else decrement
  let bitIndex = 0;
  const bits = Array.from(bitsString).map(c => c.charCodeAt(0) & 1); // simple mapping for demo
  const blockSize = 8;
  for (let by=0; by<h; by += blockSize) {
    for (let bx=0; bx<w; bx += blockSize) {
      if (bitIndex >= bits.length) break;
      // compute simple luminance average on block to approximate DCT basis presence
      let sum = 0;
      for (let y=0;y<blockSize;y++) for (let x=0;x<blockSize;x++) {
        const idx = ((by+y)*w + (bx+x)) * 4;
        const r = data[idx], g = data[idx+1], b = data[idx+2];
        const Y = 0.299*r + 0.587*g + 0.114*b;
        sum += Y;
      }
      const avg = sum / (blockSize*blockSize);
      const delta = bits[bitIndex] ? 1.5 : -1.5; // very small delta
      // apply delta uniformly across block's pixels' luminance (imperceptible)
      for (let y=0; y<blockSize; y++) for (let x=0;x<blockSize;x++){
        const idx = ((by+y)*w + (bx+x)) * 4;
        data[idx] = clamp(data[idx] + delta);
        data[idx+1] = clamp(data[idx+1] + delta);
        data[idx+2] = clamp(data[idx+2] + delta);
      }
      bitIndex++;
    }
    if (bitIndex >= bits.length) break;
  }
  return imgData;
}

export function extractDCTWatermark(imgData, bitsLen=8) {
  // reverse of above: read per-block average sign to reconstruct bits
  const w = imgData.width, h = imgData.height;
  const data = imgData.data;
  const blockSize = 8;
  const bits = [];
  for (let by=0; by<h; by += blockSize) {
    for (let bx=0; bx<w; bx += blockSize) {
      if (bits.length >= bitsLen) break;
      let sum = 0;
      for (let y=0;y<blockSize;y++) for (let x=0;x<blockSize;x++) {
        const idx = ((by+y)*w + (bx+x)) * 4;
        const r = data[idx], g = data[idx+1], b = data[idx+2];
        const Y = 0.299*r + 0.587*g + 0.114*b;
        sum += Y;
      }
      const avg = sum / (blockSize*blockSize);
      bits.push(avg > 128 ? 1 : 0);
    }
    if (bits.length >= bitsLen) break;
  }
  return bits;
}

function clamp(v) {
  return Math.max(0, Math.min(255, Math.round(v)));
}
